import {KeyValueObject} from '@bmc-ux/adapt-angular';

// TODO: Refactor this if possible
/* eslint-disable @typescript-eslint/no-explicit-any */
export const ObjectUtils = {
  // copied logic from primeng utils
  resolveFieldData(data: KeyValueObject, field: string | ((data: any) => any)): any {
    if (data && field) {
      if (typeof field === 'function') {
        return field(data);
      } else if (field.indexOf('.') === -1) {
        return data[field];
      } else {
        const fields = field.split('.');
        let value = data;

        for (let i = 0, len = fields.length; i < len; ++i) {
          if (value == null) {
            return null;
          }
          value = value[fields[i]];
        }

        return value as any;
      }
    } else {
      return null;
    }
  }
};

// TODO: use the adapt version after the package update
export const HtmlUtils = {
  hasShrunkText(element: HTMLElement): boolean {
    return element.offsetWidth < element.scrollWidth;
  },
};
